import React from 'react';
import Fade from 'react-reveal/Fade';
import Flash from 'react-reveal/Flash';

const Footer = () => {
    return (
        <footer className="bck_red">
            <Fade delay={50}>
                <Flash>
                        <div className="font_righteous footer_logo_venue">May be two hands produce noise,One is enough to create music
                            Let's sound with beats
                        </div>
                </Flash>
                <div className="footer_copyright">
                    All rights reserved.
                </div>
            </Fade>
        </footer>
    );
};

export default Footer;


